.container {
  position: relative;
  min-width: 320px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 20px;
  height: 100%;
}
@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}
@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 32px;
  }
}
@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
    padding: 0 20px;
  }
}
